:root {
  --color-white: #fff;
  --color-light: #fafafa;
  --color-lightgray: #1111111a;
  --color-gray: #111111b2;
  --color-dark: #1e1823;
  --color-black: #111;
  --color-orange: #fd9222;
  --color-lightorange: #fcf1e0;
}

body {
  background-color: var(--color-light);
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

button {
  cursor: pointer;
}

.second-heading {
  letter-spacing: -.03em;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 1.2;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (width >= 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (width >= 1200px) {
  .container {
    max-width: 1200px;
  }
}

.header-container {
  width: 335px;
  margin: 0 auto;
  padding: 0 20px;
}

.header-navigation {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-logo > a {
  align-items: center;
  gap: 4px;
  padding: 25px 0 19px;
  display: flex;
}

.header-chocolate-icon {
  fill: #0000;
  stroke: var(--color-orange);
  stroke-width: 1.8px;
  width: 21px;
  height: 21px;
}

.header-nav-box > ul {
  gap: 24px;
  display: none;
}

.header-logo-text {
  white-space: nowrap;
  color: var(--color-black);
  letter-spacing: -.03em;
  font-size: 14px;
  font-style: italic;
  font-weight: 700;
  line-height: 1.28;
}

.header-chocolate {
  font-style: normal;
}

.header-nav-list-item {
  letter-spacing: -.02em;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.14;
}

.header-social-media-box > ul {
  gap: 18px;
  display: none;
}

.header-burger-menu {
  height: 28px;
  display: block;
}

.header-burger-button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 4px;
  padding: 0;
}

.header-burger-icon {
  fill: #0000;
  stroke: var(--color-black);
  stroke-width: 2px;
  width: 28px;
  height: 28px;
}

