.hero-background {
  color: var(--color-white);
  background-image: linear-gradient(#1110, #111), url("hero-background-mobile.3fe8c82d.jpg");
  background-repeat: no-repeat;
  border-radius: 30px;
  flex-wrap: wrap;
  align-items: end;
  height: 621px;
  margin-bottom: 71px;
  padding: 21px 20px;
  display: flex;
}

.hero-heading {
  text-transform: none;
